	module.exports = {"data":{"site":{"siteMetadata":{"title":"MyBlog"}},"markdownRemark":{"html":"<p>El protocolo de comunicación MMS del estándar IEC 61850 es utilizado en sistemas\nde energía y es utilizado principalmente en Europa.</p>\n<h2>Servidor MMS</h2>\n<p>Como sistema de desarrollo se utiliza una máquina virtual con Ubuntu 17.10.\nPrimero, se requiere descargar el repositorio desde Github:</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\"><pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token function\">git</span> clone https://github.com/mz-automation/libiec61850.git</code></pre></div>\n<p>Después, vamos a compilar para la arquitectura ARM utilizando el compilador\ngcc-arm-linux el ejemplo que viene en la carpeta demos/beaglebone:</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\"><pre style=\"counter-reset: linenumber 0\" class=\"language-bash line-numbers\"><code class=\"language-bash\"><span class=\"token function\">sudo</span> <span class=\"token function\">apt-get</span> <span class=\"token function\">install</span> gcc-arm-linux-gnueabi gcc-arm-linux-gnueabihf\n<span class=\"token function\">cd</span> demos/beaglebone\n<span class=\"token function\">make</span> TARGET<span class=\"token operator\">=</span>LINUX-ARM\n<span class=\"token function\">scp</span> beagle_demo debian@192.168.7.2:/var/lib/cloud9</code><span aria-hidden=\"true\" class=\"line-numbers-rows\" style=\"white-space: normal; width: auto; left: 0;\"><span></span><span></span><span></span><span></span></span></pre></div>\n<p>Finalmente, hacemos una conexión mediante SSH con la Beaglebone black y\ncorremos el programa:</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\"><pre style=\"counter-reset: linenumber 0\" class=\"language-bash line-numbers\"><code class=\"language-bash\"><span class=\"token function\">sudo</span> ./var/lib/cloud9/beagle_demo</code><span aria-hidden=\"true\" class=\"line-numbers-rows\" style=\"white-space: normal; width: auto; left: 0;\"><span></span></span></pre></div>\n<h2>Cliente MMS</h2>\n<p>Ahora requerimos compilar el cliente MMS que va a correr en la máquina\nvirtual de Ubuntu. Primero modificamos el archivo Makefile para que compile\nel programa del cliente MMS cambiando estas líneas:</p>\n<div class=\"gatsby-highlight\" data-language=\"makefile\"><pre style=\"counter-reset: linenumber 0\" class=\"language-makefile line-numbers\"><code class=\"language-makefile\">PROJECT_BINARY_NAME <span class=\"token operator\">=</span> beagle_client\nPROJECT_SOURCES <span class=\"token operator\">=</span> beagle_client.c</code><span aria-hidden=\"true\" class=\"line-numbers-rows\" style=\"white-space: normal; width: auto; left: 0;\"><span></span><span></span></span></pre></div>\n<p>Ahora compilamos el programa del cliente MMS:</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\"><pre style=\"counter-reset: linenumber 0\" class=\"language-bash line-numbers\"><code class=\"language-bash\"><span class=\"token function\">make</span> TARGET<span class=\"token operator\">=</span>LINUX-ARM</code><span aria-hidden=\"true\" class=\"line-numbers-rows\" style=\"white-space: normal; width: auto; left: 0;\"><span></span></span></pre></div>\n<p>Finalmente, corremos el programa del cliente MMS en la máquina virtual Ubuntu:</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\"><pre style=\"counter-reset: linenumber 0\" class=\"language-bash line-numbers\"><code class=\"language-bash\"><span class=\"token function\">sudo</span> ./beagle_client <span class=\"token punctuation\">[</span>DIRECCION_IP_BEAGLE<span class=\"token punctuation\">]</span></code><span aria-hidden=\"true\" class=\"line-numbers-rows\" style=\"white-space: normal; width: auto; left: 0;\"><span></span></span></pre></div>","frontmatter":{"title":"Servidor MMS IEC 61850 en una Beaglebone Black","author":"Maximiliano Valencia","date":"15 marzo, 2018"}}},"pathContext":{"slug":"/servidor_iec_61850_mms_beaglebone_black/"}}

